import React from 'react';
import { Link } from 'react-router-dom';
import './css/about.css';

// import Button from 'antd/lib/button';
import {
    Button,
    Input,
    Layout
} from 'antd';

const InputGroup = Input.Group;
const { Header, Footer, Sider, Content } = Layout;

class AboutPanel extends React.Component {

    render() {
        return (
            <div className='about-panel'>
                <h3 className='about-title'>{this.props.title}</h3>
                {this.props.subTitle}
                <p>{this.props.desc}</p>

                {this.props.children}
            </div>
        );
    }

}

class AboutPanelSpecial extends React.Component{

    render() {
        return (
            <AboutPanel title=""
                        subTitle={<h4>,</h4>}
                        desc="," />
        );
    }

}

class About extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            input: ''
        }
    }


    handleInput = (e)=>{
        console.log(e.target.value);
        this.setState({
            input: e.target.value
        })
    };

    render() {
        return (
            <div className='about'>


                <AboutPanel title=""
                            desc=",,."/>

                <AboutPanel title=""
                            desc=",,,,,..." >

                    <InputGroup>
                        <Input type="text" style={{width: '70%'}} value={this.state.input} onChange={this.handleInput}/>
                        <Button type='primary' onClick={()=> console.log(this.state.input)}></Button>
                    </InputGroup>

                </AboutPanel>

                <AboutPanelSpecial />

            </div>
        );
    }
}

export default About;



